        /**
          * Get Scroll Offset
          *
          * @return bool
          *---------------------------------------------------------------- */
        getScrollOffsets : function() {
            var doc = document, w = window;
            var x, y, docEl;
            
            if ( typeof w.pageYOffset === 'number' ) {
                x = w.pageXOffset;
                y = w.pageYOffset;
            } else {
                docEl = (doc.compatMode && doc.compatMode === 'CSS1Compat')?
                        doc.documentElement: doc.body;
                x = docEl.scrollLeft;
                y = docEl.scrollTop;
            }
            return {x:x, y:y};
        },

