var __ngSupport = {
        getText:function(string, replaceObj) {

            if (replaceObj && _.isObject(replaceObj)) {

                _.forIn(replaceObj, function(value, key) {
                    string = string.replace(key, value);
                });
            }

             return string;
        }
};

